/*global*/
body {
  margin: 0;
  height: 100%;
  background: #f1f1f1;
}

html {
  height: 100%;
}

ul{
  padding-left: 0px;
  padding-top: 10px;
}
nav {
  height: 100%;
  float: left;
  width: 210px;
  margin-right:100px;
  position: fixed;
  margin-top: 50px;
}
main {
  height: 100%;
  float: right;
  width: calc(100% - 310px);
}
.middle-helper {
  height: 100%;
}
.container {
    height: calc(100% - 64px);
    color: #7b7b7b;
}
/*HEADER*/
#google-plus {
  height: 100%;

  .main-header {
    background: #ffffff;
    height: 64px;
    box-shadow: 0 1px 8px rgba(0, 0, 0, .3);
    z-index: 1;
    color: #7b7b7b;
    position: fixed;
    width: 100%;
    > * {
      display: inline-block;
      vertical-align: middle;
    }

    .fa-bars {
      padding: 10px;
      cursor: pointer;
      margin-left: 10px;
      margin-right: 20px;
      font-size: 20px;
    }
    .logo {
      font-size: 26px;
      font-weight: normal;
    }
    .homepage {
      border-left: 1px solid rgba(0, 0, 0, .12);
      padding-left: 24px;
      margin-left: 24px;
    }
    h1, h2 {
      margin: 0;
    }

    .searchbar {
      height: 48px;
      padding-left: 30px;
      position: relative;
      width: calc(100% - 450px);
      padding-right: 10px;
      .fa-search {
        position: absolute;
        top: 33%;
        left: 20px;
        // transform: translateY(-50%);
        margin-left: 25px;
        color: #C4C4C4;
      }
      input{
        display: inline-block;
        width: 100%;
        border: none;
        background: #e5e5e5;
        height: 48px;
        line-height: 48px;
        border-radius: 4px;
        font-size: 20px;
        padding-left: 50px;
        &::placeholder {
          color: #C4C4C4;
          font-size: 18px;
          font-weight: normal;
        }
        &:focus{
          outline: none;
        }
      }
    }
    .fa-th-large, .fa-bell-o {
      margin: 5px;
    }
    .account {
      background: orange;
      width: 32px;
      height: 32px;
      border-radius: 50%;
      text-align: center;
      margin-right: 20px;
      span {
        color: #ffffff;
        font-size: 20px;
        height: 100%;
        line-height: 32px;
        display: inline-block;
      }
    }
  }
}

/*nav*/
#google-plus {
  .container {
    .fa {
      height: 20px;
      width: 28px;
      padding-right: 10px;
      padding-left: 20px;
      font-size: 20px;
      vertical-align: middle;
    }
    li {
      list-style-type: none;
      display: inline-block;
      font-size: 15px;
      padding: 0;
      height: 20px;
      width: 120px;
      vertical-align: middle;
      margin-top: 15px;
      margin-bottom: 15px;
    }
    .block:hover {
      width: 100%;
      height: 50px;
      background-color: #D3D3D3;
    }
    .line {
      width: 100%;
      height: 1px;
      background-color: rgba(0, 0, 0, .12);
      margin: 8px 0;
      display: inline-block;
    }
    .now {
      color: #311b92;
    }
    .fa-compass {
      font-size: 24px;
    }
    .fa-home {
      font-size: 22px;
    }
    a {
      color: inherit;
    }
    .no-icon {
      padding-left: 20px;
      font-size: 15px;
      font-weight: normal;
      color: rgba(0,0,0,0.50);
    }
    .footer {
      bottom: 50px;
      position: absolute;
      font-size: 10px;
      padding: 25px;
      color: rgba(0,0,0,0.40);
    }
  }
}
/*article*/
#google-plus {
  #article {
    display: inline-block;
    width: 80%;
    text-align: center;
    padding-top: 90px;
    margin-left: 200px;
    .post{
      width: calc(80% - 90px);
      vertical-align: middle;
      margin: 0 auto;
      position: relative;
      .inputaccount {
        position: absolute;
        margin-top: 9px; 
      }
      input{
        display: inline-block;
        width: 100%;
        border: none;
        background: white;
        height: 60px;
        line-height: 60px;
        border-radius: 4px;
        font-size: 20px;
        padding-left: 60px;
        // position: absolute;
        &::placeholder {
          color: #C4C4C4;
          font-size: 18px;
          font-weight: normal;
        }
        &:focus{
          outline: none;
        }
        // .inputaccount {
        //   float: left;
        //   display: inline-block;
        //   position: relative;
        // }
      }
      .post1 {
        margin-top: 20px;
        background: white;
        height: 50px;
        text-align: left;
        line-height: 50px;
        vertical-align: middle;
        h4{
          display: inline-block;
          padding-left: 3px;
          margin: 0;
          float: left;
        }
        h5 {
          display: inline-block;
          float: right;
          margin: 0;
          padding-right: 15px;
        }
        p {
          background: white;
          padding: 10px;
          margin: 0;
          display: inline-block;
          line-height: 30px;
        }
        img {
          float: left;
          padding-top: 3px;
        }
        .postimg{
          padding: 0;
          width: 100%;
        }
      }
    }
  }
}
